<template>
  <div class="login">
      <div class="box-text">欢迎登录</div>
      <div class="logo">
          <img class="auto-img" src="../assets/logo.png" alt="">
      </div>
    <van-form @submit="login">
      <van-field class="box-dz"
        v-model="userInfo.phone"
        name="手机号"
        label="手机号"
        placeholder="请输入手机号"
        autocomplete="off"
      />
      <van-field class="box-dz"
        v-model="userInfo.password"
        type="password"
        name="密码"
        label="密码"
        placeholder="请输入密码：8-16字符"
        autocomplete="off"
      />
      <div class="tex-zh">
       <span @click="goPage('Email')">找回密码</span>
       </div>
      <div class="btn-box">
        <van-button class="btn-cor" color="#D79F52" round block type="info" native-type="submit">登录</van-button>
      </div>
      <div class="tex-zc">
        <span @click="goPage('Register')">注册</span>|<span  @click="goPage('Menu')">返回菜单</span>
      </div>
    </van-form>
  </div>
</template>

<script>
//导入表单验证模块
import {valid} from '../utils/valid'

//导入公共方法模块
import {utils} from '../utils/utils'

export default {
  name: "Login",

  computed: {
    userInfo() {
      return this.$store.state.loginModule.userInfo;
    }
  },

  methods: {
    //登录
    login() {
      

      if (valid.validForm(this.userInfo, this)) {

        //发起ajax请求
        

        //加载提示
        this.$toast.loading({
          
          //是否禁止背景点击
          forbidClick: true,

          //禁止自动关闭
          duration: 0,

          message: '登录中...'

        })

        this.axios({
          method: 'POST',
          url: '/login',
          data: {
            appkey: this.appkey,
            phone: this.userInfo.phone,
            password: this.userInfo.password
          },
          transformRequest: utils.transformRequest
        }).then(result => {
          

          //关闭加载提示
          this.$toast.clear();

          //如果登录成功后, 将token保存在cookie中
          if (result.data.code == 200) {
           
            
            //存储token字符串
            localStorage.setItem('_t', result.data.token);

            //跳转页面
            this.$router.push({name: 'Menu'});

          } else {
            this.$toast(result.data.msg);
          }

        }).catch(err => {
          
          this.$toast.clear();
        })

      }

      
    },

    goPage(routerName) {
      this.$router.push({name: routerName});
    }
  }
};
</script>

<style lang="less" scoped>

.login { 
    background: #3B2212;
    height: 667px !important;
    position: relative;
    
  .btn-box {
    margin-top: 16px;
    padding: 35px 20px;

  }
  .logo{
      margin: 50px auto;
      width: 140px;
      height: 140px;
      border-radius:50%;
      border: 1px solid #ddd;
      background: white;
     
  }
  .box-text{
      font-size: 30px;
      text-align: center;
      padding-top: 80px;
      color:white;
  }
  .box-dz{
      margin: 0 auto;
      margin-bottom: 20px !important;
      width: 90% !important;
  }
  .tex-zh{
    float: right;
    margin-right: 6%;
    font-size: 14px;
    color: #ffffff;
  }
  .tex-zc{
    text-align: center;
    color: #ffffff;
    span{
      color: #ffffff;
      margin-right: 5px;
      margin-left: 5px;
      font-size: 15px;
    }
  }
}
</style>